*{
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

body {
  padding: 32px;
}

input {
  width: 100%;
  font-size: 16px;
  color: grey;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border:  1px solid grey;
  border-radius: 8px;
  overflow: hidden;
  margin-bottom: 16px;
  margin-right: 16px; 
  width: 200px;
}

.card p {
  padding:  4px;
  text-align: center;
}

.card img {
  width: 100%;
}